import React, { FC } from "react";
import { ThemeEvents, useWhiteTheme } from "providers";
import { decorationList } from "./components";

interface Props {
  eventName: ThemeEvents;
}

export const EventDecoration: FC<Props> = ({ eventName }) => {
  const { activeEvent } = useWhiteTheme();

  const Component = decorationList[eventName];

  return <Component />;
};
